.progress-bar {
  position: relative;
  height: 25px;
  width: 350px;
  border-radius: 50px;
  border: 1px solid #1DA598;
}

.filler {
  background: #1DA598;
  height: 100%;
  border-radius: inherit;
  transition: width .2s ease-in;
}

button {
  margin-right: 10px;
  padding: 7px 12px;
  font-size: 14px;
  background: #1DA598;
  color: white;
  cursor: pointer;
  outline: 0;
}

.tooltip {
  position: relative;
  display: inline-block;
}

.tooltiptext {
  border: 1px solid;
}

h2 {margin-bottom: 3rem;}

.tooltip .tooltiptext {
  visibility: hidden;
  width: 120px;
  background-color: #eee;
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;
  bottom: 2rem;
  left: 50%;
  margin-left: -60px;
  position: absolute;
  z-index: 1;
}

.tooltip:hover .tooltiptext {
  transition-delay:500ms;
  visibility: visible;
}
